<template>
  <div
    class="contact"
    :style="{ transform:data.transform, left:data.left, top:data.top }"
    @click="onClick(data)"
  >
    <nav class="active">
      <a href="#" class="twitter">
        <div class="content">
          <h1>{{data.name}}</h1>
          <div class="icon">
            <img :src="'https://favicon.link/' + data.url" width="12" height="12" alt />
          </div>
          <span>{{domain(data.url)}}</span>
        </div>
        <svg
          class="arrow"
          xmlns="http://www.w3.org/2000/svg"
          width="48"
          height="48"
          viewBox="0 0 48 48"
        >
          <g class="nc-icon-wrapper" fill="#444444">
            <path d="M17.17 32.92l9.17-9.17-9.17-9.17L20 11.75l12 12-12 12z" />
          </g>
        </svg>
      </a>
    </nav>
  </div>
</template>

<script>
export default {
  name: "Nav",
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  directives: {},
  methods: {
    encode(param) {
      try {
        return encodeURIComponent(btoa(param));
      } catch (error) {
        // console.log(error);
      }
      return "";
    },
    domain(url) {
      let domain = url.split("/");
      if (domain[2]) {
        domain = domain[2];
      } else {
        domain = "";
      }
      return domain;
    },
    onClick(data) {
      window.location.href = data.url;
    }
  }
};
</script>

<style scoped>
body {
  font-family: "Roboto Condensed", sans-serif;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #051321;
  font-family: "Roboto Condensed", sans-serif;
}

.contact {
  z-index: 1;
  width: 260px;
  margin: 20px 20px;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  transition: 1s;
  position: absolute;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
}

.contact:hover {
  z-index: 2;
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.2), 0 10px 26px 0 rgba(0, 0, 0, 0.19);
  transition: .5s;
  transform: rotate(0deg) scale(1.1);
}

.contact nav {
  width: 300px;
  overflow: hidden;
}

.contact nav a {
  float: left;
  width: 100%;
  margin-top: 18px;
  background-color: #ffffff;
  padding: 18px 20px;
  border-radius: 5px;
  text-decoration: none;
  position: relative;
}

.contact nav a .arrow {
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
}

.contact nav a .arrow path {
  fill: #e6e6e6;
}

.contact nav a .content h1 {
  color: #1da1f2;
}

.contact nav a .icon {
  float: left;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 5px;
}

.contact nav a .content h1 {
  font-size: 20px;
  font-weight: 400;
  line-height: 160%;
  letter-spacing: 0.4px;
}
.contact nav a .content span {
  font-size: 14px;
  color: #b3b3b3;
  display: block;
  margin-top: 2px;
  letter-spacing: 0.4px;
}
</style>
