<template>
  <div
    class="contact"
    :style="{ transform:data.transform, left:data.left, top:data.top }"
    @click="onClick(data)"
  >
    <main>
      <section
        :style="{ 'background-image': 'linear-gradient(to bottom right,rgba(' + data.color2 + ', .9),rgba(' + data.color + ', 1))' }"
      >
        <div class="content">
          <aside>
            <h1>{{data.title}}</h1>
          </aside>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
export default {
  name: "CheatSheet",
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  directives: {},
  methods: {
    encode(param) {
      try {
        return encodeURIComponent(btoa(param));
      } catch (error) {
        // console.log(error);
      }
      return "";
    },
    onClick(data) {
      window.location.href =
        "/markdown?id=" +
        this.encode(data.id) +
        "&bg=" +
        this.encode(data.color);
    }
  }
};
</script>

<style scoped>
body {
  font-family: "Roboto Condensed", sans-serif;
}

.contact {
  z-index: 1;
  width: 260px;
  margin: 20px 20px;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  transition: 1s;
  position: absolute;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
}

.contact:hover {
  z-index: 2;
  box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.2), 0 10px 26px 0 rgba(0, 0, 0, 0.19);
  transition: 1s;
  transform: rotate(0deg) scale(1.1);
}

.contact main {
  float: left;
  width: 100%;
  position: relative;
}

.contact main section {
  border-radius: 5px;
  float: left;
  width: 100%;
}

.contact main section .content {
  float: left;
  width: 100%;
  margin: 20px;
  position: relative;
  text-align: left;
}

.contact main section .content aside {
  float: left;
  width: 100%;
  color: #ffffff;
}

.contact main section .content aside h1 {
  font-weight: 100;
  font-size: 16px;
}
</style>
